<script>
export default {
  name: "arrow",
  props: {
    hiddenRight: {
      type: Boolean,
      default: false
    },
    hiddenLeft: {
      type: Boolean,
      default: false
    },
    hover: {
      type: Boolean,
      default: false
    },
    isMobile: {
      type: Boolean,
      default: false
    }
  }
}
</script>

<template>
  <div>
    <div @click="$emit('left-click')" v-show="(!hiddenLeft && hover) || (!hiddenLeft && isMobile)"
         class="animate__fadeInDown animate__animated animate__faster
         absolute top-1/2 left-4 transform -translate-y-1/2 border-2 rounded-full border-white z-50 cursor-pointer">
      <img class="w-10 h-10" src="@/assets/icon/toLeft.png" alt="">
    </div>
    <div @click="$emit('right-click')" v-show="(!hiddenRight && hover) || (!hiddenRight && isMobile)"
         class="animate__fadeInDown animate__animated animate__faster
         transition-all absolute top-1/2 right-4 transform -translate-y-1/2  border-2 rounded-full border-white z-50 cursor-pointer">
      <img class="w-10 h-10" src="@/assets/icon/toRight.png" alt="">
    </div>
  </div>
</template>

