<script>
import arrow from "@/components/arrow.vue";

export default {
  name: "boutique",
  components: {arrow},
  data() {
    return {
      heightList: [
        {
          img: require("@/assets/images/门店简介配图/猫屎咖啡家族精品店/height (1).jpg"),
          title: 0
        },
        {
          img: require("@/assets/images/门店简介配图/猫屎咖啡家族精品店/height (2).jpg"),
          title: 1
        },
        {
          img: require("@/assets/images/门店简介配图/猫屎咖啡家族精品店/height (3).jpg"),
          title: 2
        },
        {
          img: require("@/assets/images/门店简介配图/猫屎咖啡家族精品店/height (4).jpg"),
          title: 3
        },
        {
          img: require("@/assets/images/门店简介配图/猫屎咖啡家族精品店/height (5).jpg"),
          title: 4
        },
        {
          img: require("@/assets/images/门店简介配图/猫屎咖啡家族精品店/height (6).jpg"),
          title: 5
        },
        {
          img: require("@/assets/images/门店简介配图/猫屎咖啡家族精品店/height (7).jpg"),
          title: 6
        }
      ],
      widthList: [
        {
          img: require("@/assets/images/门店简介配图/猫屎咖啡家族精品店/width (1).jpg"),
          title: 0
        },
        {
          img: require("@/assets/images/门店简介配图/猫屎咖啡家族精品店/width (2).jpg"),
          title: 1
        },
        {
          img: require("@/assets/images/门店简介配图/猫屎咖啡家族精品店/width (3).jpg"),
          title: 2
        },
        {
          img: require("@/assets/images/门店简介配图/猫屎咖啡家族精品店/width (4).jpg"),
          title: 3
        },
        {
          img: require("@/assets/images/门店简介配图/猫屎咖啡家族精品店/width (5).jpg"),
          title: 4
        }
      ],
      hWidth: 0,
      wWidth: 0,
      curHTranslateX: 0,
      curWTranslateX: 0,
      heightHover: false,
      widthHover: false,
      isMobile: false
    }
  },
  methods: {
    heightMove(num) {
      this.curHTranslateX += num;
      this.$refs.heightBoxRef.style.transform = `translateX(${this.curHTranslateX}px)`;
    },
    widthMove(num) {
      this.curWTranslateX += num;
      this.$refs.widthBoxRef.style.transform = `translateX(${this.curWTranslateX}px)`;
    }
  },
  mounted() {
    const heightRef = this.$refs.heightRef;
    this.hWidth = heightRef[0].offsetWidth;

    const widthRef = this.$refs.widthRef;
    this.wWidth = widthRef[0].offsetWidth;

    if (window.innerWidth <= 640) {
      this.isMobile = true;
    }
  }
}
</script>

<template>
  <div>
    <div class="w-screen bg-coffeePink h-[80vh]">
      <div class="container mx-auto h-full flex flex-col sm:flex-row items-center py-20 lg:py-24 sm:px-20 px-5">
        <div
            class="sm:w-1/2 w-full flex flex-col justify-center order-2 sm:order-1  animate__animated animate__slideInLeft">
          <p class="font-bold text-2xl sm:text-4xl text-coffee sm:text-left text-center mt-5 sm:mt-0">
            {{ $t("coffee.boutique.title1") }}
            <br :class="{'hidden':$i18n.locale !=='en'}"/>
            {{ $t("coffee.boutique.title2") }}
          </p>
          <p class="text-sm text-coffee w-full sm:w-3/4 lg:w-5/6 mt-2 leading-6">
            {{ $t("coffee.boutique.content") }}
          </p>
        </div>
        <div class="sm:w-1/2 w-full mt-10 order-1 sm:order-2  animate__animated animate__slideInRight">
          <img class="w-full" src="@/assets/p.png" alt="">
        </div>
      </div>
    </div>
    <div class="w-full bg-white my-20">
      <div class="container mx-auto h-full sm:px-20  animate__animated animate__fadeInUp">
        <div class="w-full relative overflow-x-auto hidden-scrollbar" @mousemove="heightHover = true"
             @mouseleave="heightHover = false">
          <div class="flex w-full h-96 sm:h-102 lg:h-84 md:h-84 transition-all" ref="heightBoxRef">
            <div v-for="item in heightList" :key="item.id" class="sm:w-1/4 w-1/2 px-2 box-border h-full flex-shrink-0"
                 ref="heightRef">
              <img class="w-full h-full" :src="item.img" alt="">
            </div>
          </div>
          <arrow :isMobile="isMobile" :hover="heightHover"
                 :hidden-right="curHTranslateX === -((heightList.length - 4) *hWidth)"
                 :hidden-left="curHTranslateX ===0"
                 @left-click="heightMove(hWidth)"
                 @right-click="heightMove(-hWidth)"/>
        </div>
        <div class="font-bold text-coffee text-4xl text-center py-10">
          {{ $t("coffee.boutique.title1") }}{{ $t("coffee.boutique.title2") }}
        </div>
        <div class="w-full flex relative overflow-x-auto hidden-scrollbar" @mousemove="widthHover = true"
             @mouseleave="widthHover = false">
          <div class="flex h-96 sm:h-102 lg:h-84 md:h-84 w-full transition-all" ref="widthBoxRef">
            <div v-for="item in widthList" :key="item.id" class="sm:w-1/2 w-full px-2 box-border h-full flex-shrink-0"
                 ref="widthRef">
              <img class="w-full h-full" :src="item.img" alt="">
            </div>
          </div>
          <arrow :isMobile="isMobile" :hover="widthHover"
                 :hidden-right="curWTranslateX === -((widthList.length - 2) *wWidth)"
                 :hidden-left="curWTranslateX ===0"
                 @left-click="widthMove(wWidth)"
                 @right-click="widthMove(-wWidth)"/>
        </div>
      </div>
    </div>
  </div>
</template>

<style scoped lang="scss">

</style>